.sidebox {
    @extend .my-sm-4,
    .my-md-4,
    .p-0,
    .bg-primary-gradient,
    .text-white;
    img {
        @extend .img-fluid,
        .p-0,
        .m-0,
        .mb-2;
    }
    div {
        @extend .p-4,
        .m-0,
        .text-white;
        h5 {
            @extend .text-uppercase,
            .text-white,
            .m-2;
        }
        p {
            @extend //.section__§,
            .text-uppercase,
            .text-white,
            .m-2;
        }
        a {
            @extend .section__link,
            .text-uppercase,
            .text-white,
            .m-2;
        }
    }
}