/*
* section rules
*/

.section {
    //  @extend .m-0,.p-0;
    padding-top: $navbar-height;
    padding-bottom: $navbar-height;
    overflow-x: hidden;
    &__col {
        @extend .col-12;
        @extend .text-left;
        @extend .mx-auto;
        p {
            @media (min-width: $screen-lg-min) {
                font-size: 24px;
            }
        }
        &--left {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
        &--right {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
    }
    &__title {
        @extend .display-4;
    }
    &__subtitle {
        font-size: 32px;
    }
    &__§ {
        font-size: 16px;
        &--bold {
            @extend .font-weight-bold;
        }
        @media (min-width: $screen-lg-min) {
            font-size: 24px;
        }
    }
    &__link {
        @extend .text-uppercase;
        //text-decoration: underline;
        &:hover {
            //border-bottom: solid $primary 2px;
        }
    }
}

.container-top {
    @extend .container;
    &--top {
        @extend .text-center;
        @extend .p-4;
    }
}


/*
* centered section rules
*/

.section--centered {
    @extend .section;
    @extend .my-4;
    &__container {
        @extend .container;
    }
    &__row {
        @extend .row;
    }
    &__col {
        @extend .col-sm-12;
        @extend .col-md-12;
        @extend .col-lg-6;
        @extend .col-xl-6;
        @extend .text-center;
        @extend .mx-auto;
    }
    &__title {
        @extend .display-2;
    }
    &__subtitle {
        font-size: 36px;
    }
    &__img {
        @extend .img-fluid;
        @extend .my-4;
        @extend .mx-auto;
    }
    &__link {
        @extend .text-uppercase;
        text-decoration: underline;
    }
    &__btn {}
    &--fluid {
        @extend .container-fluid;
    }
}


/*
* left section rules
*/

.section--left {
    @extend .section;
    @extend .my-4;
    &__container {
        @extend .container;
    }
    &__col {
        @extend .section__col;
        &--left {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
        &--right {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
    }
    &__title {
        @extend .display-2;
    }
    &__link {
        @extend .text-uppercase;
        text-decoration: underline;
    }
    &--fluid {
        @extend .container-fluid;
    }
}

.section--image--left {
    @extend .section--left;
    &__col {
        &--left {
            @extend .my-auto,
            .col-sm-12,
            .col-md-6,
            .col-lg-3,
            .col-xl-3;
            img {
                @extend .img-fluid,
                .m-0,
                .my-sm-4,
                .p-0; //.w-100; //, //.h-100;
                object-fit: cover;
            }
        }
        &--right {
            @extend .col-sm-12,
            .col-md-6,
            .col-lg-9,
            .col-xl-9;
        }
    }
}


/*
* right section rules
*/

.section--right {
    @extend .section;
    @extend .my-4;
    &__container {
        @extend .container;
    }
    &__col {
        @extend .col-12;
        @extend .text-left;
        @extend .mx-auto;
        &--left {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
        &--right {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
    }
    &__title {
        @extend .display-2;
    }
    &__subtitle {
        font-size: 36px;
    }
    &__img {
        @extend /*.img-fluid,*/
        .m-0,
        .p-4,
        .w-100; //, //.h-100;
        object-fit: cover;
    }
    &__link {
        @extend .text-uppercase;
        text-decoration: underline;
    }
    &--fluid {
        @extend .container-fluid;
    }
}

.section--image--right {
    @extend .section--right;
    &__col {
        &--left {
            @extend .col-sm-12,
            .col-md-6,
            .col-lg-9,
            .col-xl-9;
        }
        &--right {
            @extend .my-auto,
            .col-sm-12,
            .col-md-6,
            .col-lg-3,
            .col-xl-3;
            img {
                @extend .img-fluid,
                .m-0,
                .my-sm-4,
                .p-0; //.w-100; //, //.h-100;
                object-fit: cover;
            }
        }
    }
}

.section--cta--right {
    @extend .section--right;
    &__col {
        &--left {
            @extend .col-sm-12,
            .col-md-6,
            .col-lg-9,
            .col-xl-9;
        }
        &--right {
            @extend .my-auto,
            .d-flex,
            .col-sm-12,
            .col-md-6,
            .col-lg-3,
            .col-xl-3;
            button {
                @extend .ml-auto;
            }
        }
    }
}