/*
* spirituality section
*/

.section--spirituality {
    @extend .section;
    &__title {
        @extend .display-4;
        @extend .text-dark;
        @extend .mt-0;
        @extend .mb-4;
    }
    &__§ {
        @extend .mb-4;
        //@extend .lead;
    }
    &__link {
        //  @extend .lead;
    }
    &__container {
        @extend .container;
    }
    &__row {
        @extend .row;
    }
    &__col {
        @extend .mx-auto,
        .col-sm-12,
        .col-md-12,
        .col-lg-6,
        .col-xl-6;
    }
    &__innercontainer {
        @extend .container-fluid;
        @extend .my-4;
        &__content {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-6,
            .col-xl-6;
        }
        &__img {
            @extend .container-fluid;
        }
    }
    &__overtitle {
        @extend .small;
        text-transform: italic;
    }
    &__title {
        @extend .display-4;
        @extend .text-dark;
        @extend .mt-0;
        @extend .mb-4;
    }
}