/*
* philately section
*/

.section--philately {
    //@extend .section;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center right!important;
    &__title {
        @extend .display-4;
        @extend .text-white;
        @extend .mt-4;
        @extend .mb-4;
    }
    &__§ {
        @extend .mt-4;
        @extend .mb-4;
        @extend .lead;
        @extend .text-white;
    }
    &__link {
        @extend .mt-4;
        @extend .mb-4;
        @extend .lead;
        @extend .text-white;
    }
    &__container {
        @extend .container-fluid;
    }
    &__row {
        @extend .row
    }
    &__col {
        @extend .col-sm-12;
        @extend .col-md-6;
        @extend .col-lg-6;
        @extend .col-xl-6;
        &--left {
            @extend .bg-primary;
            @extend .text-left;
            @extend .text-white;
            @extend .d-flex;
            min-height: 480px;
            padding-top: $navbar-height;
            padding-bottom: $navbar-height;
            @media screen and (max-width: $screen-md-min) {
                min-height: 320px;
            }
            div {
                @extend .p-2,
                .col-8,
                .mx-auto;
            }
            p {
                //   @extend .mx-auto;
            }
        }
        &--right {
            @extend .d-flex;
            @extend .text-center;
            @extend .p-2;
            min-height: 480px;
            background: linear-gradient(90deg, $primary, rgba(0, 0, 0, 0));
            @media screen and (max-width: $screen-md-min) {
                max-height: 160px;
                background: linear-gradient(180deg, $primary, rgba(0, 0, 0, 0));
            }
        }
    }
    &__img {
        @extend .rounded-circle;
        @extend .my-auto;
        @extend .mx-auto;
    }
    @media screen and (max-width: $screen-md-min) {
        //background-size: 0%;
    }
}