/*
* +notfound section
*/

.section--notfound {
    @extend .section;
    @media screen and (max-width: $screen-md-min) {
        //margin-top: -64px;
        padding-top: $spacer*4;
        padding-bottom: $spacer*4;
        margin-top: 0;
    }
    &__container {
        @extend .container-fluid;
        @extend .align-items-center;
        display: flex;
        align-items: center;
        justify-content: center;
        //min-height: 100%;
        padding-top: 128px;
        padding-bottom: 144px;
        //padding-bottom: 25vh;
        margin: 0;
        @media screen and (max-width: $screen-md-min) {
            padding-top: $spacer*4;
            padding-bottom: $spacer*4;
        }
    }
    &__row {
        @extend .row,
        .no-gutters;
    }
    &__col {
        @extend .col-sm-12,
        .col-md-12,
        .col-lg-6,
        .col-xl-6,
        .mx-auto,
        .p-0;
    }
    &__title {
        @extend .display-1;
        position: relative;
        z-index: 3;
        color: $dark;
        text-align: left;
        //text-transform: uppercase;
        font-weight: 400;
        @media screen and (max-width: $screen-lg-min) {
            font-size: 48px;
            //margin-top: -65px;
        }
    }
    &__subtitle {
        position: relative;
        z-index: 3;
        color: $dark;
        text-align: left;
        //text-transform: uppercase;
        font-weight: 400;
        font-size: 36px;
        font-family: $font-family-sans-serif;
        @media screen and (max-width: $screen-md-min) {
            font-size: 26px;
        }
    }
    &__content {
        font-size: 24px;
        color: $gray-default;
        @media screen and (max-width: $screen-md-min) {
            font-size: 16px;
        }
    }
    &__link {
        font-size: 24px;
        color: $primary;
        @media screen and (max-width: $screen-md-min) {
            font-size: 16px;
        }
    }
    &__cta {
        @extend .btn--fa;
        @extend .text-center,
        .mx-auto,
        .mt-4;
    }
    &__img {
        @extend .mx-auto;
    }
}