/*WPML//*/

.language-list {
    li {
        margin-left: 8px;
        white-space: nowrap;
    }
}

.language-list-item {
    display: block;
    a {
        color: $gray-600;
        white-space: nowrap;
        font-weight: 800;
    }
}

li.wpml-ls-item:not(.wpml-ls-current-language) {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}

li.wpml-ls-item:hover {
    filter: grayscale(0%);
}


/* Slider */

.slick-slide {
    margin: 0px 20px;
}

.slick-slide img {
    width: 100%;
}

.slick-track::before,
.slick-track::after {
    display: table;
    content: '';
}

.slick-track::after {
    clear: both;
}

.slick-track {
    padding: 1rem 0;
}

.slick-loading .slick-track {
    visibility: hidden;
}

.slick-slide.dragging img {
    pointer-events: none;
}

.slick-loading .slick-slide {
    visibility: hidden;
}


/*
* +Gravity
*/

.gf_left_half:first-child()+.gf_right_half {
    margin-top: 0;
}

.gform_footer {
    text-align: center;
}

.ginput_container {
    input,
    textarea {
        border: 1px solid #000;
    }
}

.gfield {
    &:first-child {
        margin-top: 0 !important;
    }
    label {
        font-size: 14px !important;
        font-weight: 400 !important;
        color: $primary;
    }
}

.gform_body {
    width: calc(100% + 15px) !important;
}

.gform_wrapper {
    @media (min-width: 480) {
        margin-top: 0 !important;
    }
}

.gfield_required {
    display: none;
}

.gform_button {
    @extend .btn;
    margin: 0 !important;
}