@import "../../variables";
.btn-primary {
    background: $btn-gradient;
}

.btn {
    display: inline-block;
    //font-size: 24px;
    font-weight: 600;
    font-family: $headings-font-family;
    text-decoration: none;
    padding: $spacer;
    //min-width: 160px;
    //line-height: 1.5;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    /*
    border: 4px solid $blue-light;
    border-width: 0.125em;
    border-radius: $spacer*2;
//*/
    outline: 0;
    transition: all .2s ease;
    &:focus,
    &:hover {
        color: $primary;
        background: $white;
        text-decoration: none;
        outline: 0;
        box-shadow: $box-shadow;
    }
    &--primary {
        @extend .btn-primary;
        @extend .text-uppercase;
        @extend .text-white;
        font-size: 20px;
        //color: $white;
        min-width: $spacer*8;
        background: $primary-gradient;
        font-weight: 600;
        border: $spacer solid $primary;
        border-width: 0.125em;
        border-radius: $spacer*2;
        span {
            @extend .m-0,
            .p-0;
            // background: $white;
        }
        &:focus,
        &:hover {
            @extend .bg-primary,
            .text-uppercase,
            .text-white;
            border-color: $blue-light;
            background: $primary;
            color: white;
            text-decoration: none;
            outline: 0;
            box-shadow: $box-shadow;
        }
        //
    }
    &--cta {
        @extend .py-2,
        .px-3,
        .bg-white,
        .text-uppercase,
        .text-primary;
        font-weight: 500;
        border: 4px solid;
        border-width: 0.25em;
        border-radius: $spacer*2;
        span {
            @extend .m-0,
            .p-0;
            // background: $white;
        }
        &:focus,
        &:hover {
            @extend .bg-primary-gradient,
            .text-uppercase,
            .text-white;
            background: $primary-gradient;
            color: white;
            text-decoration: none;
            outline: 0;
            box-shadow: $box-shadow;
        }
        //
    }
    &--prayer {
        @extend .text-center;
        height: $navbar-height;
        width: $navbar-height;
        padding: $navbar-height/4;
        background: $blue-light;
        border-width: 0.5em;
        border-radius: $navbar-height;
        padding: $spacer;
        border-color: #fff;
        &__img {}
        &:focus,
        &:hover {
            @extend .bg-primary-gradient;
            background: $primary-gradient;
            color: white;
            text-decoration: none;
            outline: 0;
            box-shadow: $box-shadow;
        }
        @media screen and (max-width: $screen-md-min) {
            height: $navbar-height/2;
            width: $navbar-height/2;
            border-width: 0.125em;
            border-radius: $navbar-height/2;
            &__img {
                max-height: 48px;
                max-width: 48px;
            }
        }
    }
    &--fa {
        border: $spacer solid $primary;
        border-width: 0;
        border-radius: $spacer*2;
        background: $blue-light;
        height: 32px;
        width: 32px;
        .fa {
            color: $primary;
        }
    }
    &--secondary {
        @extend .btn-secondary;
        font-size: 20px;
        color: $dark;
        background: $white;
        font-weight: 200;
    }
    &--inverse {
        color: #fff;
        background-color: $primary;
        &:focus,
        &:hover {
            background: transparent;
            color: $primary;
            border-color: $primary;
        }
    }
    &--fullwidth {
        width: 100%;
    }
    &--big {
        font-size: 18px;
        padding: 8px 24px;
    }
    @media screen and (max-width: $screen-md-min) {
        padding: $spacer;
        font-size: 14px;
    }
    &--small {
        font-size: 11px;
        padding: 3px 6px;
    }
    &--loading {
        &:after {
            content: "";
            display: inline-block;
            width: .8em;
            height: .8em;
            margin-left: 5px;
            border: 2px solid currentColor;
            border-bottom: 2px solid transparent;
            border-radius: 50%;
            animation: fa-spin 1s infinite linear;
        }
    }
}

@keyframes fa-spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(359deg);
    }
}