/*
* team section rules
*/

.section--team {
    @extend .my-4;
    &__container {
        @extend .container;
    }
    &__row {
        @extend .row;
        @extend .d-flex;
        @extend .justify-content-center;
        @extend .mx-auto;
        @extend .text-center;
    }
    &__col {
        @extend .section__col;
        @extend .text-center;
        @extend .mx-auto;
    }
    &__member-col {
        @extend .col-sm-6,
        .col-md-6,
        .col-lg-4,
        .col-xl-2,
        .my-4,
        .d-flex;
        &__item {
            @extend .mx-auto,
            .text-center;
            img {
                @extend .rounded-circle,
                .mb-4;
                height: 160px;
                width: 160px;
                display: block;
            }
        }
        &__name {
            @extend .text-dark, //.dontbreak,
            .text-uppercase;
        }
        &__role {
            @extend .text-gray,
            .text-uppercase;
        }
    }
    &__subtitle {
        font-size: 36px;
    }
    &__img {
        @extend .img-fluid;
        @extend .my-4;
        @extend .mx-auto;
    }
    &__link {
        @extend .text-uppercase;
        text-decoration: underline;
    }
    &--fluid {
        @extend .container-fluid;
    }
}