/*
* introduction block
*/

.page-introduction {
    @extend .container,
    .my-4;
    &__container {
        @extend .container;
    }
    &__col {
        @extend .text-left;
        &--left {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
        &--right {
            @extend .m-0,
            .p-0,
            .col-sm-12,
            .col-md-12,
            .col-lg-3,
            .col-xl-3;
        }
    }
    &__title {
        @extend .display-4;
    }
    &__subtitle {
        font-size: 32px;
    }
    &__link {
        @extend .text-uppercase;
        text-decoration: underline;
    }
}