/*
* calltoaction section
*/

.section--cta {
    @extend .section;
    &__§ {
        @extend .display-3;
        @extend .my-4;
        @extend .text-dark;
        @extend .text-sm-center;
        @extend .text-md-center;
        @extend .text-lg-left;
        @extend .text-xl-left;
    }
    &__container {
        @extend .container-fluid;
        //&--top {}
    }
    &__row {
        @extend .row;
        @extend .d-flex;
        @extend .align-items-center;
        @extend .mx-auto;
        @extend .h-100;
        @extend .w-100;
        @extend .px-sm-0;
        @extend .py-sm-4;
        @extend .px-md-0;
        @extend .py-md-4;
        @extend .px-lg-4;
        @extend .py-lg-4;
        @extend .px-xl-4;
        @extend .py-xl-4;
        //@extend .p-4;
        //@extend .w-100;
        //@extend .no-gutters;
    }
    &__col--img {
        @extend .col-sm-12,
        .col-md-12,
        .col-lg-4,
        .col-xl-4,
        .pt-sm-2,
        .py-lg-4,
        /*.mx-auto*/
        .text-center;
    }
    &__col {
        @extend .col-sm-12,
        .col-md-12,
        .col-lg-8,
        .col-xl-8,
        .pt-sm-2,
        .py-lg-4;
        @extend .text-sm-center;
        @extend .text-md-center;
        @extend .text-lg-left;
        @extend .text-xl-left;
        /*.mx-auto,*/
        @media screen and (max-width: $screen-md-min) {
            text-align: center!important;
        }
    }
    &__bottom {
        @extend .col-sm-12,
        .col-md-12,
        .col-lg-12,
        .col-xl-12,
        .pt-sm-2,
        .py-lg-4,
        /*.mx-auto,*/
        .text-center;
    }
    &__img {
        @extend .my-4,
        .img-fluid;
    }
}