/*
* gallery section
*/

.section--gallery {
    //@extend .section;
    &__title {
        @extend .display-4;
    }
    &__link {}
    &__container {
        @extend .container-fluid;
        @extend .m-0;
        @extend .p-0,
        .mx-auto;
    }
    &__row {
        @extend .row,
        .mx-lg-auto;
    }
    &__col {
        @extend .col-sm-12;
        @extend .col-md-12;
        @extend .col-lg-6;
        @extend .col-xl-6;
    }
    &__grid {
        @extend .container,
        .d-flex,
        .flex-wrap;
        &__img {
            //@extend .my-2;
            @extend .p-1;
            @extend .col-sm-1,
            .col-md-2,
            .col-lg-4,
            .col-xl-4;
            // @extend .img-fluid;
            // max-height: 180px;
            //object-fit: cover;
        }
    }
    &__carousel {
        @extend .m-0,
        .w-100;
        .carousel-inner {
            max-height: 40vh!important;
            overflow-y: hidden;
        }
        &__img {
            @extend /*.img-fluid,*/
            .m-0,
            .p-0,
            .w-100; //, //.h-100;
            //object-fit: cover;
            width: 50vw;
            min-height: 80vh;
        }
    }
}