/*
* +Hero section
*/

.hero {
    @extend .section;
    @extend .h-100;
    @extend .m-0;
    @extend .p-0;
    //background-size: 50%;
    background-repeat: no-repeat;
    background-position-x: right;
    background-position-y: top;
    padding-top: $navbar-height!important;
    padding-bottom: $navbar-height !important;
    @media screen and (min-width: $screen-lg-min) {
        background-size: 80%;
        background-position-x: 45vw;
        background-position-y: top;
        min-height: 100vh;
    }
    @media screen and (max-width: $screen-lg-min) {
        padding-top: $navbar-height !important;
        background-size: 100%;
        background-position-x: -30vw;
        background-position-y: center;
        min-height: 640px;
    }
    @media screen and (max-width: $screen-sm-min) {
        background-size: 0%;
    }
    &__container {
        @extend .container;
        // padding: $navbar-height*2 $navbar-height/2 $navbar-height*2 $navbar-height/2;
    }
    &__row {
        @extend .row;
        @extend .d-flex;
        @extend .align-items-center;
        @extend .mx-auto;
        @extend .h-100;
        @extend .w-100;
        @extend .px-sm-0;
        @extend .py-sm-4;
        //@extend .p-4;
        //@extend .w-100;
        //@extend .no-gutters;
    }
    &__col {
        @extend .col-sm-12,
        .col-md-12,
        .col-lg-6,
        .col-xl-6,
        .pt-sm-2,
        .pt-lg-4, //        .offset-xl-2
        ;
        /*.mx-auto,*/
    }
    &__col--video {
        @extend .col-sm-12,
        .col-md-12,
        .col-lg-6,
        .col-xl-6,
        .d-flex,
        .text-center;
        @extend .my-sm-4;
        @extend .my-md-4;
        iframe {
            @extend .mx-auto;
            @extend .my-2;
        }
    }
    &__title {
        @extend .display-1,
        .text-left,
        .dontbreak,
        .text-dark;
        font-weight: 300;
        @media screen and (max-width: $screen-md-min) {
            font-size: 48px!important;
        }
    }
    &__subtitle {
        @extend .text-left,
        .text-dark;
        //text-transform: uppercase;
        font-weight: 600;
        font-size: 36px;
        line-height: 1.6em;
        @media screen and (max-width: $screen-md-min) {
            font-size: 26px;
            line-height: 2em;
        }
    }
    &__§ {
        @extend .lead;
        font-size: 24px;
        color: $gray-default;
        font-family: $font-family-sans-serif;
        @media screen and (max-width: $screen-md-min) {
            font-size: 20px;
        }
    }
    &__link {
        @extend .my-4;
        //font-size: 24px;
        color: $primary;
        @media screen and (max-width: $screen-md-min) {
            font-size: 16px;
        }
    }
    &__cta {
        @extend .text-center,
        .mx-auto;
        margin-top: $navbar-height;
        margin-bottom: $navbar-height;
    }
    &--page {
        min-height: 768px;
        background-size: cover!important;
        &__overlay {
            min-height: 768px;
            @extend .bg-primary-transparent;
            @extend .h-100,
            .m-0;
            padding-top: $navbar-height;
            padding-bottom: $navbar-height;
            height: 100%;
        }
        &__container {
            @extend .container;
        }
        &__row {
            @extend .row;
        }
        &__col {
            @extend .col-sm-12,
            .col-md-12,
            .col-lg-9,
            .col-xl-9;
        }
        &__title {
            @extend .display-2,
            .text-left, //.dontbreak,
            .text-white;
            font-weight: 300;
            // text-shadow: 2px 2px #111111;
        }
        &__subtitle {
            @extend .text-left,
            .text-white;
            font-weight: 700;
            font-size: 36px;
            //text-shadow: 2px 2px #111111;
            @media screen and (max-width: $screen-md-min) {
                font-size: 26px;
            }
        }
        &__§ {
            @extend .lead;
            @extend .text-white;
            font-size: 24px;
            color: $gray-default;
            font-family: $font-family-sans-serif;
            //text-shadow: 2px 2px #111111;
            @media screen and (max-width: $screen-md-min) {
                font-size: 20px;
            }
        }
    }
}